      h2 3.2. Conteo, agrupación y control de usuarios

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p El conteo, la agrupación y el control de usuarios son procesos fundamentales para garantizar la seguridad, la organización y una experiencia de calidad durante un recorrido turístico.
      p Estas acciones permiten al guía turístico mantener la trazabilidad de los visitantes, evitar extravíos y promover un desplazamiento armónico dentro del entorno natural o cultural visitado. Según la Organización Mundial del Turismo (OMT, 2019), “la gestión responsable de los flujos turísticos es esencial para preservar el equilibrio entre visitantes, comunidades locales y ecosistemas”. Por ello, el control de usuarios no se limita a un aspecto logístico, sino que constituye una práctica de sostenibilidad humana y ambiental.
      p Ahora, se profundizará en el conteo, agrupación y control de usuarios para explicar que implica cada uno de estos procesos:

