              .col-md-7
                p.color-adicional-4 El conteo de usuarios se realiza antes, durante y después del recorrido, utilizando listas de verificación, registros digitales o conteo visual directo. Su objetivo es garantizar la seguridad del grupo y asegurar que todos los participantes sean contabilizados correctamente en cada punto de control.
                p.color-adicional-4 Ejemplo: Antes de iniciar una caminata ecológica en territorio indígena, el guía verifica el número de participantes y repite el conteo en cada estación o punto de descanso.

