      p En los recorridos realizados con comunidades indígenas o afrodescendientes, el conteo y la agrupación también se integran a los protocolos de respeto y acompañamiento espiritual, donde cada visitante es considerado un invitado del territorio. En muchos casos, los guías locales inician con una ceremonia o saludo de armonización antes del conteo formal, reforzando la conexión entre el ser humano y la naturaleza.

